import React, { Component } from "react";
import logo from "../img/amana-logo.png";

class Login extends Component {
 state = { custom: true };
 render() {
  return (
   <div className="d-flex flex-fill flex-column main-cover justify-content-center">
    <div className="container">
     <div className="row justify-content-center">
      <div className="col-xl-10 col-lg-12 col-md-9">
       <div className="card o-hidden border-0 shadow-lg my-5">
        <div className="card-body p-0">
         <div className="row">
          <div className="col-lg-6 d-lg-flex d-none  default-bg d-flex align-items-center justify-content-center flex-row">
           <img src={logo} alt="" className="muted d-none d-md-flex" />
          </div>
          <div className="col-lg-6">
           <div className="p-5">
            <div className="text-center">
             <h1 className="h4 text-gray-900 mb-4 font-weight-bold">
              Amana Capital
             </h1>
            </div>

            <div className="text-center mb-4">
             <small className="text-muted">Admin login</small>
            </div>

            <div className="form-group">
             <input
              type="email"
              className="form-control form-control-user"
              placeholder="Enter Email Address..."
              onChange={e => {
               this.setState({ email: e.target.value });
              }}
             />
            </div>
            <div className="form-group">
             <input
              type="password"
              className="form-control form-control-user"
              placeholder="Password"
              onChange={e => {
               this.setState({ password: e.target.value });
              }}
             />
            </div>
            <div className="form-group">
             <div className="custom-control custom-checkbox small">
              <input type="checkbox" className="custom-control-input" checked />
              <label className="custom-control-label" for="customCheck">
               Remember Me
              </label>
             </div>
            </div>
            <button
             className="btn btn-danger btn-user btn-block default-bg"
             onClick={this.login}
            >
             Login
            </button>

            <hr />
           </div>
          </div>
         </div>
        </div>
       </div>
      </div>
     </div>
    </div>
   </div>
  );
 }

 login = () => {
  let { email } = this.state;
  let { password } = this.state;
  console.log(this.state);

  if (!(email !== "" && password !== "")) {
   alert("Please fill in all the required values");
   return false;
  }

  this.setState({ disabled: true });
  // let data = { name, email, password };

  fetch(window.server + "/login.php", {
   method: "POST",
   headers: {
    // "Content-Type": "application/json",
    "Access-Control-Allow-Origin": "*",
    "Content-Type": "application/x-www-form-urlencoded"
   },
   body: `email=${email}&password=${password}`
  })
   .then(response => response.json())
   .then(response => {
    console.log(response);
    // response = JSON.parse(response);
    console.log(response);
    this.setState({ disabled: false });

    if (response.code === "200") {
     localStorage.id = response.data.id;
     localStorage.user = JSON.stringify(response.data);
     window.user = response.data;

     this.props.history.push("/");

     return false;
    }

    alert("Wrong Email/password. please try again");
    this.setState({ password: "" });
   });
 };
}

export default Login;
