import React, { Component } from "react";
import { Search, Edit, Trash, Pause } from "react-feather";

class Table extends Component {
 // eslint-disable-next-line
 state = {
  titles: [],
  data: [],
  limit: 10,
  offset: 0,
  count: 1,
  currentPagination: 0,
  custom: false,
  customText: ""
 };
 render() {
  return (
   <>
    <div className="d-flex flex-row justify-content-between align-items-center buttons-tab">
     <button
      className="btn btn-sm btn-outline-dark mr-2"
      onClick={() => {
       this.setState({ custom: false, currentPagination: 0, customText: "" });
       setTimeout(() => {
        this.fetchTable();
       }, 0);
      }}
     >
      All users
     </button>
     <button
      className="btn btn-sm btn-outline-dark mr-2"
      onClick={() => {
       this.setState({
        custom: true,
        currentPagination: 0,
        customText: " (income - (needs + wants))<10000 "
       });
       setTimeout(() => {
        this.fetchTableCustom();
       }, 0);
      }}
     >
      savings &lt; 10,000
     </button>
     <button
      className="btn btn-sm btn-outline-dark mr-2"
      onClick={() => {
       this.setState({
        custom: true,
        currentPagination: 0,
        customText: " (income - (needs + wants))>10000 "
       });
       setTimeout(() => {
        this.fetchTableCustom();
       }, 0);
      }}
     >
      savings &gt; 10,000
     </button>
     <button
      className="btn btn-sm btn-outline-dark mr-2"
      onClick={() => {
       this.setState({
        custom: true,
        currentPagination: 0,
        customText: " id>1 "
       });
       setTimeout(() => {
        this.fetchTableCustom();
       }, 0);
      }}
     >
      Leads
     </button>
     <button
      className="btn btn-sm btn-outline-dark mr-2"
      onClick={() => {
       this.setState({
        custom: true,
        currentPagination: 0,
        customText: " phone='undefined' or phone = '' "
       });
       setTimeout(() => {
        this.fetchTableCustom();
       }, 0);
      }}
     >
      no phone
     </button>
    </div>
    <div
     className={
      "d-flex flex-fill flex-column " +
      (this.state.tableLoading ? "table-loading" : "")
     }
    >
     <div className="d-flex flex-row justify-content-between align-items-center">
      <div className="d-flex flex-row align-items-center">
       <span className="mr-2">Show </span>
       <select
        className="form-control form-control-sm"
        onChange={event => {
         this.setState({ limit: parseInt(event.target.value) });
         console.log(this.state.limit);
         setTimeout(() => {
          this.fetchTable();
         }, 100);
        }}
       >
        <option value={10}>10</option>
        <option value={25}>25</option>
        <option value={50}>50</option>
        <option value={100}>100</option>
       </select>
       <span className="ml-2"> entries </span>
      </div>
     </div>

     <div className="d-flex flex-fill tb-cover">
      <table className="table table-striped text-dark table-borderless">
       <thead>
        <tr>
         {this.state.titles.map((title, i) => (
          <th key={i} className="text-capitalize">
           {title}
          </th>
         ))}
        </tr>
       </thead>
       <tbody>
        {this.state.data.map((data, index) => (
         <tr key={index}>
          {Object.keys(data).map((d, i) => {
           let o = data[d] == null || data[d] == "" ? " - " : data[d];

           return (
            <td key={i} className="table-data">
             {o}
            </td>
           );
          })}
          {/* <td className="d-flex flex-row align-items-center py-2 h-100">
           <div
            className="px-2 menu-icon"
            data-toggle="tooltip"
            data-placement="top"
            title="Edit"
           >
            <Edit size={17} />
           </div>
           <div
            className="px-2 menu-icon"
            data-toggle="tooltip"
            data-placement="top"
            title="Disable"
           >
            <Pause size={17} />
           </div>
          </td> */}
         </tr>
        ))}
       </tbody>
      </table>
     </div>
     <div className="d-flex flex-row justify-content-between align-items-center">
      <div className="d-flex flex-row align-items-center">
       <span className="mr-2">
        Showing {this.state.offset} to {this.state.offset + this.state.limit} of{" "}
        {this.state.count} entries{" "}
       </span>
      </div>
      <div>
       <nav aria-label="Page navigation example">
        <ul className="pagination">
         <li className="page-item">
          <button
           className="page-link"
           onClick={() => {
            this.setState({ offset: 0, currentPagination: 0 });
            setTimeout(() => {
             this.fetchTable();
            }, 100);
           }}
          >
           &laquo;
          </button>
         </li>
         <li
          className="page-item"
          onClick={() => {
           if (this.state.currentPagination - 1 >= 0) {
            this.setState({
             offset: (this.state.currentPagination - 1) * this.state.offset,
             currentPagination: this.state.currentPagination - 1
            });
           }
           setTimeout(() => {
            this.fetchTable();
           }, 100);
          }}
         >
          <button className="page-link">&lt;</button>
         </li>

         {this.generatePagination(this.state.currentPagination)}

         <li className="page-item">
          <button
           className="page-link"
           onClick={() => {
            if (
             this.state.currentPagination + 1 <
             parseInt(this.state.count / this.state.limit)
            ) {
             this.setState({
              offset: (this.state.currentPagination + 1) * this.state.limit,
              currentPagination: this.state.currentPagination + 1
             });
             setTimeout(() => {
              this.fetchTable();
             }, 100);
            }
           }}
          >
           &gt;
          </button>
         </li>
         <li className="page-item">
          <button
           className="page-link"
           onClick={() => {
            this.setState({
             offset: this.state.count - this.state.limit,
             currentPagination: parseInt(this.state.count / this.state.limit)
            });
            setTimeout(() => {
             this.fetchTable();
            }, 100);
           }}
          >
           &raquo;
          </button>
         </li>
        </ul>
       </nav>
      </div>
     </div>
    </div>
   </>
  );
 }

 componentDidMount() {
  this.fetchTable();
 }

 // eslint-disable-next-line

 fetchTable = () => {
  if (this.state.custom) {
   this.fetchTableCustom();
   return false;
  }
  this.fetchTableCount();
  this.setState({ tableLoading: true });
  console.log(
   `${window.server}/fetchData.php?t=${this.props.table}&l=${
    this.state.limit
   }&o=${this.state.offset}`
  );
  fetch(
   `${window.server}/fetchData.php?t=${this.props.table}&l=${
    this.state.limit
   }&o=${this.state.offset}`
  )
   .then(response => response.json())
   .then(data => {
    this.setState({ tableLoading: false });
    data = data.data;
    console.log(data);
    if (this.props.filter) {
     data = data.map(d => {
      this.props.filter.map(c => {
       delete d[c];
      });
      return d;
     });
     //   console.log(data);
    }
    let titles = Object.keys(data[0]);

    this.setState({ data, titles: [...titles] });
   })
   .catch(d => {
    this.setState({ tableLoading: false });
    console.log(d);
   });
 };

 fetchTableCustom = () => {
  this.fetchTableCount();
  this.setState({ tableLoading: true });
  console.log(
   `${window.server}/fetchData.php?t=${this.props.table}&l=${
    this.state.limit
   }&o=${this.state.offset}`
  );

  let formData = new FormData();
  formData.append("t", this.props.table);
  formData.append("l", this.state.limit);
  formData.append("o", this.state.offset);
  formData.append("custom", this.state.customText);

  fetch(`${window.server}/fetchCustomData.php?`, {
   method: "POST",
   body: formData
  })
   .then(response => response.json())
   .then(data => {
    this.setState({ tableLoading: false });
    console.log(data);
    data = data.data;
    if (this.props.filter) {
     data = data.map(d => {
      this.props.filter.map(c => {
       delete d[c];
      });
      return d;
     });
     //   console.log(data);
    }
    let titles = Object.keys(data[0]);

    this.setState({ data, titles: [...titles] });
   })
   .catch(d => {
    this.setState({ tableLoading: false });
    console.log(d);
   });
 };

 fetchTableCount = () => {
  console.log(`${window.server}/tablecount.php?t=${this.props.table}`);
  fetch(`${window.server}/tablecount.php?t=${this.props.table}`)
   .then(response => response.json())
   .then(data => {
    // console.log(data);
    this.setState({ count: data.data.count });
    // console.log(this.state.count);
   })
   .catch(d => {
    console.log(d);
   });
 };

 generatePagination() {
  console.log("currentPagination :", this.state.currentPagination);
  let count = parseInt(this.state.count);
  let { limit } = this.state;
  let { offset } = this.state;

  let pagination = [];

  let number = count / limit;
  let position = parseInt(number * (offset / count));
  position -= 2;
  if (position < 0) position = 0;
  let n = position;

  while (position < n + 5) {
   let pos = position;
   pagination.push(
    <li
     key={pos}
     className={
      "page-item " + (this.state.currentPagination == position ? "active" : "")
     }
    >
     <button
      className="page-link"
      onClick={() => {
       console.log("next position :: ", pos);
       let offset = pos * limit;
       this.setState({ offset, currentPagination: pos });
       setTimeout(() => {
        this.fetchTable();
       }, 100);
      }}
     >
      {pos + 1}
     </button>
    </li>
   );
   position++;
  }

  return pagination;
 }
}

export default Table;
